import React, { useState, useEffect, createContext, useContext } from 'react';
import PropTypes from 'prop-types';
import { Shield, Plus, Trash2, Lock, Video, Search, X, Check, Eye, EyeOff } from 'lucide-react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import { debounce } from 'lodash';
import { v4 as uuidv4 } from 'uuid';

// Constants
const YOUTUBE_API_BASE = 'https://www.googleapis.com/youtube/v3';
const MAX_RESULTS = 20;
const MIN_PIN_LENGTH = 4;
const MAX_PIN_LENGTH = 6;
const DEBOUNCE_DELAY = 200;

// Context for managing app state
const AppContext = createContext(undefined);

const useApp = () => {
  const context = useContext(AppContext);
  if (!context) {
    throw new Error('useApp must be used within AppProvider');
  }
  return context;
};

// AppProvider Component
const AppProvider = ({ children }) => {
  const [approvedChannels, setApprovedChannels] = useState([]);
  const [parentPin, setParentPin] = useState(''); // No default PIN
  const [isParentMode, setIsParentMode] = useState(false);
  const [videos, setVideos] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [loadingMore, setLoadingMore] = useState(false);
  const [nextPageTokens, setNextPageTokens] = useState({});
  const [apiKey, setApiKey] = useState(() => localStorage.getItem('youtubeApiKey') || '');

  // Load data from localStorage on mount
  useEffect(() => {
    const savedChannels = localStorage.getItem('approvedChannels');
    const savedPin = localStorage.getItem('parentPin');
    
    if (savedChannels) {
      setApprovedChannels(JSON.parse(savedChannels));
    }
    if (savedPin) {
      setParentPin(savedPin);
    } else {
      // Prompt for initial PIN setup if none exists
      setParentPin(''); // Will trigger PIN setup in Settings
    }
  }, []);

  // Save approved channels to localStorage
  useEffect(() => {
    localStorage.setItem('approvedChannels', JSON.stringify(approvedChannels));
  }, [approvedChannels]);

  // Save API key to localStorage
  useEffect(() => {
    if (apiKey) {
      localStorage.setItem('youtubeApiKey', apiKey);
    }
  }, [apiKey]);

  // Save PIN to localStorage
  useEffect(() => {
    if (parentPin) {
      localStorage.setItem('parentPin', parentPin);
    }
  }, [parentPin]);

  // Fetch channel ID with caching
  const fetchChannelId = async (channelName) => {
    const cacheKey = `channelId_${channelName}`;
    const cachedId = localStorage.getItem(cacheKey);
    if (cachedId) return cachedId;

    try {
      const response = await fetch(
        `${YOUTUBE_API_BASE}/search?part=snippet&type=channel&q=${encodeURIComponent(channelName)}&key=${apiKey}`
      );
      if (!response.ok) {
        if (response.status === 403) throw new Error('Invalid API key or quota exceeded');
        throw new Error(`YouTube API error: ${response.status}`);
      }
      const data = await response.json();
      const channelId = data.items?.[0]?.snippet.channelId;
      if (channelId) {
        localStorage.setItem(cacheKey, channelId);
        return channelId;
      }
      throw new Error(`Channel "${channelName}" not found`);
    } catch (err) {
      throw err;
    }
  };

  // Fetch videos from YouTube API
  const fetchVideosFromChannels = async (loadMore = false, filter = '') => {
    if (approvedChannels.length === 0 || !apiKey) {
      setVideos([]);
      setError('No channels or API key configured');
      return;
    }

    if (!loadMore) {
      setIsLoading(true);
    } else {
      setLoadingMore(true);
    }
    setError(null);

    try {
      const allVideos = loadMore ? [...videos] : [];
      const newNextPageTokens = loadMore ? { ...nextPageTokens } : {};

      for (const channel of approvedChannels) {
        try {
          const channelId = await fetchChannelId(channel.name);
          let videosUrl = `${YOUTUBE_API_BASE}/search?part=snippet&channelId=${channelId}&type=video&order=date&maxResults=${MAX_RESULTS}&key=${apiKey}`;
          
          if (loadMore && newNextPageTokens[channelId]) {
            videosUrl += `&pageToken=${newNextPageTokens[channelId]}`;
          }
          if (filter) {
            videosUrl += `&q=${encodeURIComponent(filter)}`;
          }

          const videosResponse = await fetch(videosUrl);
          if (!videosResponse.ok) {
            throw new Error(`YouTube API error: ${videosResponse.status}`);
          }

          const videosData = await videosResponse.json();
          
          if (videosData.nextPageToken) {
            newNextPageTokens[channelId] = videosData.nextPageToken;
          } else {
            delete newNextPageTokens[channelId];
          }

          if (videosData.items) {
            const channelVideos = videosData.items.map((item) => ({
              id: item.id.videoId,
              title: item.snippet.title,
              channel: item.snippet.channelTitle,
              channelId: item.snippet.channelId,
              thumbnail: item.snippet.thumbnails.medium.url,
              publishedAt: item.snippet.publishedAt,
              description: item.snippet.description,
            }));

            if (loadMore) {
              const existingIds = new Set(allVideos.map((v) => v.id));
              const newVideos = channelVideos.filter((v) => !existingIds.has(v.id));
              allVideos.push(...newVideos);
            } else {
              allVideos.push(...channelVideos);
            }
          }
        } catch (err) {
          console.error(`Error fetching videos for channel ${channel.name}:`, err);
          if (err.message.includes('Invalid API key')) {
            setError('Invalid API key. Please check your YouTube API key in settings.');
            break;
          } else if (err.message.includes('Channel')) {
            setError(err.message);
          }
        }
      }

      allVideos.sort((a, b) => new Date(b.publishedAt).getTime() - new Date(a.publishedAt).getTime());
      setVideos(allVideos);
      setNextPageTokens(newNextPageTokens);
    } catch (err) {
      setError('Failed to load videos. Please check your API key and try again.');
      console.error('Error fetching videos:', err);
    } finally {
      setIsLoading(false);
      setLoadingMore(false);
    }
  };

  // Fetch videos when approved channels or API key changes
  useEffect(() => {
    if (apiKey && approvedChannels.length > 0) {
      fetchVideosFromChannels();
    }
  }, [approvedChannels, apiKey]);

  const value = {
    approvedChannels,
    setApprovedChannels,
    parentPin,
    setParentPin,
    isParentMode,
    setIsParentMode,
    videos,
    setVideos,
    isLoading,
    error,
    apiKey,
    setApiKey,
    fetchVideosFromChannels,
    loadingMore,
    nextPageTokens,
  };

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};

AppProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

// PIN Entry Component
const PinEntry = ({ onSuccess, onCancel }) => {
  const [pin, setPin] = useState('');
  const [error, setError] = useState('');
  const [showPin, setShowPin] = useState(false);
  const { parentPin } = useApp();

  const handleSubmit = () => {
    if (pin === parentPin) {
      onSuccess();
      setPin('');
      setError('');
    } else {
      setError('Incorrect PIN');
      setPin('');
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSubmit();
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" role="dialog" aria-labelledby="pin-entry-title">
      <div className="bg-white rounded-lg p-8 max-w-sm w-full">
        <div className="flex items-center justify-between mb-4">
          <h2 id="pin-entry-title" className="text-xl font-bold flex items-center gap-2">
            <Lock className="w-5 h-5" />
            Enter Parent PIN
          </h2>
          <button onClick={onCancel} className="text-gray-500 hover:text-gray-700" aria-label="Close PIN entry">
            <X className="w-5 h-5" />
          </button>
        </div>
        
        <div>
          <div className="mb-4">
            <div className="relative">
              <input
                type={showPin ? 'text' : 'password'}
                pattern="[0-9]*"
                inputMode="numeric"
                value={pin}
                onChange={(e) => e.target.validity.valid && setPin(e.target.value)}
                onKeyPress={handleKeyPress}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Enter PIN"
                maxLength={MAX_PIN_LENGTH}
                aria-label="Parent PIN"
              />
              <button
                type="button"
                onClick={() => setShowPin(!showPin)}
                className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-500"
                aria-label={showPin ? 'Hide PIN' : 'Show PIN'}
              >
                {showPin ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
              </button>
            </div>
            {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
          </div>
          
          <div className="flex gap-2">
            <button
              onClick={handleSubmit}
              className="flex-1 bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600"
              aria-label="Submit PIN"
            >
              Submit
            </button>
            <button
              onClick={onCancel}
              className="flex-1 bg-gray-200 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-300"
              aria-label="Cancel"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

PinEntry.propTypes = {
  onSuccess: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
};

// Channel Management Component
const ChannelManager = () => {
  const { approvedChannels, setApprovedChannels, fetchVideosFromChannels } = useApp();
  const [newChannel, setNewChannel] = useState('');
  const [showAddForm, setShowAddForm] = useState(false);
  const [channelToDelete, setChannelToDelete] = useState(null);

  const handleAddChannel = async () => {
    if (newChannel.trim()) {
      try {
        const channel = {
          id: uuidv4(),
          name: newChannel.trim(),
          addedAt: new Date().toISOString(),
        };
        setApprovedChannels([...approvedChannels, channel]);
        setNewChannel('');
        setShowAddForm(false);
        await fetchVideosFromChannels(); // Refresh videos
      } catch (err) {
        console.error('Error adding channel:', err);
      }
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleAddChannel();
    }
  };

  const handleRemoveChannel = (id) => {
    setApprovedChannels(approvedChannels.filter((channel) => channel.id !== id));
    fetchVideosFromChannels();
  };

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold">Approved Channels</h2>
        <button
          onClick={() => setShowAddForm(true)}
          className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600 flex items-center gap-2"
          aria-label="Add new channel"
        >
          <Plus className="w-4 h-4" />
          Add Channel
        </button>
      </div>

      {showAddForm && (
        <div className="mb-4 p-4 bg-gray-50 rounded-lg">
          <div className="flex gap-2">
            <input
              type="text"
              value={newChannel}
              onChange={(e) => setNewChannel(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Enter channel name"
              className="flex-1 px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500"
              aria-label="New channel name"
            />
            <button
              onClick={handleAddChannel}
              className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600"
              aria-label="Confirm add channel"
            >
              <Check className="w-4 h-4" />
            </button>
            <button
              onClick={() => {
                setShowAddForm(false);
                setNewChannel('');
              }}
              className="bg-gray-300 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-400"
              aria-label="Cancel add channel"
            >
              <X className="w-4 h-4" />
            </button>
          </div>
        </div>
      )}

      <div className="space-y-2">
        {approvedChannels.length === 0 ? (
          <p className="text-gray-500 text-center py-8">No approved channels yet. Add some channels to get started!</p>
        ) : (
          approvedChannels.map((channel) => (
            <div key={channel.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
              <span className="font-medium">{channel.name}</span>
              <button
                onClick={() => setChannelToDelete(channel)}
                className="text-red-500 hover:text-red-700"
                aria-label={`Remove ${channel.name}`}
              >
                <Trash2 className="w-4 h-4" />
              </button>
            </div>
          ))
        )}
      </div>

      {channelToDelete && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" role="dialog" aria-labelledby="confirm-delete-title">
          <div className="bg-white rounded-lg p-6 max-w-sm w-full">
            <h3 id="confirm-delete-title" className="text-lg font-semibold mb-4">
              Delete Channel
            </h3>
            <p className="text-sm text-gray-600 mb-4">
              Are you sure you want to remove <strong>{channelToDelete.name}</strong>?
            </p>
            <div className="flex gap-2">
              <button
                onClick={() => {
                  handleRemoveChannel(channelToDelete.id);
                  setChannelToDelete(null);
                }}
                className="bg-red-500 text-white px-4 py-2 rounded-lg hover:bg-red-600"
                aria-label="Confirm delete"
              >
                Delete
              </button>
              <button
                onClick={() => setChannelToDelete(null)}
                className="bg-gray-300 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-400"
                aria-label="Cancel delete"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

// Settings Component
const Settings = () => {
  const { parentPin, setParentPin, apiKey, setApiKey, fetchVideosFromChannels } = useApp();
  const [newPin, setNewPin] = useState('');
  const [confirmPin, setConfirmPin] = useState('');
  const [showPinChange, setShowPinChange] = useState(!parentPin); // Show if no PIN set
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [tempApiKey, setTempApiKey] = useState(apiKey);
  const [showApiKey, setShowApiKey] = useState(false);

  const handlePinChange = () => {
    setError('');
    setSuccess('');

    if (newPin.length < MIN_PIN_LENGTH) {
      setError(`PIN must be at least ${MIN_PIN_LENGTH} digits`);
      return;
    }

    if (newPin !== confirmPin) {
      setError('PINs do not match');
      return;
    }

    setParentPin(newPin);
    setSuccess('PIN changed successfully!');
    setNewPin('');
    setConfirmPin('');
    setTimeout(() => {
      setShowPinChange(false);
      setSuccess('');
    }, 2000);
  };

  const handleApiKeyUpdate = () => {
    setApiKey(tempApiKey);
    setSuccess('API key updated! Refreshing videos...');
    fetchVideosFromChannels();
    setTimeout(() => setSuccess(''), 3000);
  };

  return (
    <div className="space-y-6">
      <div className="bg-white rounded-lg shadow-md p-6">
        <h2 className="text-2xl font-bold mb-6">Security Settings</h2>
        <div className="space-y-4">
          <div>
            <h3 className="text-lg font-semibold mb-2">Parent PIN</h3>
            <button
              onClick={() => setShowPinChange(!showPinChange)}
              className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600"
              aria-label="Change parent PIN"
              disabled={showPinChange}
            >
              Change Parent PIN
            </button>

            {showPinChange && (
              <div className="mt-4 p-4 bg-gray-50 rounded-lg">
                <div className="space-y-3">
                  <input
                    type="text"
                    pattern="[0-9]*"
                    inputMode="numeric"
                    value={newPin}
                    onChange={(e) => e.target.validity.valid && setNewPin(e.target.value)}
                    placeholder="New PIN"
                    className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    maxLength={MAX_PIN_LENGTH}
                    aria-label="New PIN"
                  />
                  <input
                    type="text"
                    pattern="[0-9]*"
                    inputMode="numeric"
                    value={confirmPin}
                    onChange={(e) => e.target.validity.valid && setConfirmPin(e.target.value)}
                    placeholder="Confirm PIN"
                    className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    maxLength={MAX_PIN_LENGTH}
                    aria-label="Confirm PIN"
                  />
                  {error && <p className="text-red-500 text-sm">{error}</p>}
                  {success && <p className="text-green-500 text-sm">{success}</p>}
                  <div className="flex gap-2">
                    <button
                      onClick={handlePinChange}
                      className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600"
                      aria-label="Save PIN"
                    >
                      Save
                    </button>
                    <button
                      onClick={() => {
                        setShowPinChange(false);
                        setNewPin('');
                        setConfirmPin('');
                        setError('');
                      }}
                      className="bg-gray-300 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-400"
                      aria-label="Cancel PIN change"
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow-md p-6">
        <h2 className="text-2xl font-bold mb-6">YouTube API Settings</h2>
        <div className="space-y-4">
          <div>
            <h3 className="text-lg font-semibold mb-2">API Key</h3>
            <p className="text-sm text-gray-600 mb-4">
              Enter your YouTube Data API v3 key to fetch videos from approved channels.
            </p>
            <div className="space-y-3">
              <div className="relative">
                <input
                  type={showApiKey ? 'text' : 'password'}
                  value={tempApiKey}
                  onChange={(e) => setTempApiKey(e.target.value)}
                  placeholder="Enter your YouTube API key"
                  className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 pr-10"
                  aria-label="YouTube API key"
                />
                <button
                  type="button"
                  onClick={() => setShowApiKey(!showApiKey)}
                  className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-500"
                  aria-label={showApiKey ? 'Hide API key' : 'Show API key'}
                >
                  {showApiKey ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                </button>
              </div>
              <button
                onClick={handleApiKeyUpdate}
                className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600"
                aria-label="Save API key"
              >
                Save API Key
              </button>
              {success && <p className="text-green-500 text-sm">{success}</p>}
              <div className="mt-4 p-4 bg-blue-50 rounded-lg">
                <p className="text-sm text-blue-800">
                  <strong>How to get an API key:</strong>
                </p>
                <ol className="text-sm text-blue-700 mt-2 list-decimal list-inside space-y-1">
                  <li>Go to Google Cloud Console</li>
                  <li>Create a new project or select existing</li>
                  <li>Enable YouTube Data API v3</li>
                  <li>Create credentials (API Key)</li>
                  <li>Copy and paste the key here</li>
                </ol>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

// Video Grid Component (Kids View)
const VideoGrid = () => {
  const { videos, isLoading, error, approvedChannels, fetchVideosFromChannels, loadingMore, apiKey } = useApp();
  const [selectedVideo, setSelectedVideo] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const [filter, setFilter] = useState('');

  const handleScroll = debounce(() => {
    if (window.innerHeight + document.documentElement.scrollTop >= document.documentElement.offsetHeight - 50 && !loadingMore && hasMore) {
      fetchVideosFromChannels(true, filter);
    }
  }, DEBOUNCE_DELAY);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loadingMore, hasMore, videos, filter]);

  const handleFilterChange = (e) => {
    const newFilter = e.target.value;
    setFilter(newFilter);
    fetchVideosFromChannels(false, newFilter);
  };

  if (!apiKey) {
    return (
      <div className="text-center py-12">
        <div className="text-6xl mb-4">🔑</div>
        <p className="text-gray-600 mb-2">No API key configured!</p>
        <p className="text-sm text-gray-500">Ask a parent to add the YouTube API key in settings.</p>
      </div>
    );
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading videos...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded-lg">
        <p className="font-semibold">Error</p>
        <p className="text-sm">{error}</p>
      </div>
    );
  }

  if (approvedChannels.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="text-6xl mb-4">📺</div>
        <p className="text-gray-600 mb-2">No approved channels yet!</p>
        <p className="text-sm text-gray-500">Ask a parent to add some channels.</p>
      </div>
    );
  }

  if (videos.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="text-6xl mb-4">🔍</div>
        <p className="text-gray-600">No videos found from approved channels.</p>
      </div>
    );
  }

  return (
    <>
      <div className="mb-6">
        <div className="relative mb-4">
          <input
            type="text"
            value={filter}
            onChange={handleFilterChange}
            placeholder="Search videos..."
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 pl-10"
            aria-label="Search videos"
          />
          <Search className="w-5 h-5 absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" />
        </div>
      </div>

      {selectedVideo && (
        <div className="fixed inset-0 bg-black bg-opacity-90 z-50 flex items-center justify-center p-4" role="dialog" aria-labelledby="video-title">
          <div className="bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-hidden">
            <div className="flex items-center justify-between p-4 border-b">
              <h3 id="video-title" className="font-semibold text-lg line-clamp-1">{selectedVideo.title}</h3>
              <button
                onClick={() => setSelectedVideo(null)}
                className="text-gray-500 hover:text-gray-700 p-2"
                aria-label="Close video"
              >
                <X className="w-6 h-6" />
              </button>
            </div>
            <div className="relative pb-[56.25%] bg-black">
              <div className="youtube-overlay" onClick={(e) => e.stopPropagation()} />
              <iframe
                src={`https://www.youtube.com/embed/${selectedVideo.id}?rel=0&modestbranding=1&playsinline=1&iv_load_policy=3&disablekb=1&fs=0&showinfo=0&autohide=1&controls=1`}
                title={selectedVideo.title}
                className="absolute top-0 left-0 w-full h-full"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope"
                sandbox="allow-same-origin allow-scripts allow-popups allow-presentation"
                referrerPolicy="no-referrer"
              />
              <div className="youtube-overlay-bottom" onClick={(e) => e.stopPropagation()} />
            </div>
            <div className="p-4">
              <p className="text-sm text-gray-600">Channel: {selectedVideo.channel}</p>
            </div>
          </div>
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
        {videos.map((video) => (
          <div
            key={video.id}
            className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow cursor-pointer"
            onClick={() => setSelectedVideo(video)}
            role="button"
            tabIndex={0}
            onKeyPress={(e) => e.key === 'Enter' && setSelectedVideo(video)}
            aria-label={`Play ${video.title}`}
          >
            <div className="relative pb-[56.25%]">
              <LazyLoadImage
                src={video.thumbnail}
                alt={video.title}
                effect="blur"
                className="absolute top-0 left-0 w-full h-full object-cover"
              />
              <div className="absolute inset-0 bg-black bg-opacity-0 hover:bg-opacity-10 transition-opacity flex items-center justify-center">
                <div className="bg-white bg-opacity-90 rounded-full p-3 opacity-0 hover:opacity-100 transition-opacity">
                  <Video className="w-8 h-8 text-blue-600" />
                </div>
              </div>
            </div>
            <div className="p-4">
              <h3 className="font-semibold text-sm line-clamp-2 mb-1">{video.title}</h3>
              <p className="text-xs text-gray-600">{video.channel}</p>
            </div>
          </div>
        ))}
      </div>

      {loadingMore && (
        <div className="flex items-center justify-center py-8">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
          <span className="ml-3 text-gray-600">Loading more videos...</span>
        </div>
      )}
    </>
  );
};

// Install Prompt Component
const InstallPrompt = () => {
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [showInstallPrompt, setShowInstallPrompt] = useState(false);
  const [isInstalled, setIsInstalled] = useState(false);

  useEffect(() => {
    if (window.matchMedia('(display-mode: standalone)').matches) {
      setIsInstalled(true);
    }

    if (!('BeforeInstallPromptEvent' in window)) {
      return;
    }

    const handler = (e) => {
      e.preventDefault();
      setDeferredPrompt(e);
      setShowInstallPrompt(true);
    };

    window.addEventListener('beforeinstallprompt', handler);

    window.addEventListener('appinstalled', () => {
      setIsInstalled(true);
      setShowInstallPrompt(false);
    });

    return () => window.removeEventListener('beforeinstallprompt', handler);
  }, []);

  const handleInstall = async () => {
    if (!deferredPrompt) return;

    deferredPrompt.prompt();
    const { outcome } = await deferredPrompt.userChoice;

    if (outcome === 'accepted') {
      setDeferredPrompt(null);
      setShowInstallPrompt(false);
    }
  };

  const handleDismiss = () => {
    setShowInstallPrompt(false);
  };

  if (isInstalled || !showInstallPrompt) return null;

  return (
    <div className="fixed bottom-4 left-4 right-4 md:left-auto md:right-4 md:w-96 bg-blue-600 text-white p-4 rounded-lg shadow-lg z-50" role="dialog" aria-labelledby="install-prompt-title">
      <div className="flex items-start justify-between">
        <div className="flex-1 mr-4">
          <h3 id="install-prompt-title" className="font-bold text-lg mb-1">Install YouTube Kids Safe</h3>
          <p className="text-sm opacity-90">
            Install our app for easy access from your home screen!
          </p>
        </div>
        <button
          onClick={handleDismiss}
          className="text-white opacity-70 hover:opacity-100"
          aria-label="Dismiss install prompt"
        >
          <X className="w-5 h-5" />
        </button>
      </div>
      <div className="flex gap-2 mt-3">
        <button
          onClick={handleInstall}
          className="bg-white text-blue-600 px-4 py-2 rounded font-medium hover:bg-blue-50"
          aria-label="Install now"
        >
          Install Now
        </button>
        <button
          onClick={handleDismiss}
          className="text-white opacity-80 hover:opacity-100 px-4 py-2"
          aria-label="Maybe later"
        >
          Maybe Later
        </button>
      </div>
    </div>
  );
};

// Main App Component
const App = () => {
  const [showPinEntry, setShowPinEntry] = useState(false);
  const [activeTab, setActiveTab] = useState('videos');

  return (
    <AppProvider>
      <AppContent
        showPinEntry={showPinEntry}
        setShowPinEntry={setShowPinEntry}
        activeTab={activeTab}
        setActiveTab={setActiveTab}
      />
    </AppProvider>
  );
};

AppContent.propTypes = {
  showPinEntry: PropTypes.bool.isRequired,
  setShowPinEntry: PropTypes.func.isRequired,
  activeTab: PropTypes.oneOf(['videos', 'channels', 'settings']).isRequired,
  setActiveTab: PropTypes.func.isRequired,
};

const AppContent = ({ showPinEntry, setShowPinEntry, activeTab, setActiveTab }) => {
  const { isParentMode, setIsParentMode } = useApp();

  useEffect(() => {
    const handleContextMenu = (e) => {
      if (!isParentMode) {
        e.preventDefault();
      }
    };

    const handleSelectStart = (e) => {
      if (!isParentMode) {
        e.preventDefault();
      }
    };

    const handleClick = (e) => {
      if (!isParentMode && e.target instanceof HTMLAnchorElement) {
        e.preventDefault();
      }
    };

    if (!isParentMode) {
      document.body.style.userSelect = 'none';
      document.body.style.webkitUserSelect = 'none';
      document.body.style.mozUserSelect = 'none';
      document.body.style.msUserSelect = 'none';

      const style = document.createElement('style');
      style.id = 'kids-mode-style';
      style.innerHTML = `
        .youtube-overlay {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          height: 60px;
          z-index: 20;
          cursor: default;
        }
        .youtube-overlay-bottom {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          height: 50px;
          z-index: 20;
          cursor: default;
        }
      `;
      document.head.appendChild(style);
    } else {
      document.body.style.userSelect = 'auto';
      document.body.style.webkitUserSelect = 'auto';
      document.body.style.mozUserSelect = 'auto';
      document.body.style.msUserSelect = 'auto';

      const style = document.getElementById('kids-mode-style');
      if (style) {
        style.remove();
      }
    }

    document.addEventListener('contextmenu', handleContextMenu);
    document.addEventListener('selectstart', handleSelectStart);
    document.addEventListener('click', handleClick, true);

    return () => {
      document.removeEventListener('contextmenu', handleContextMenu);
      document.removeEventListener('selectstart', handleSelectStart);
      document.removeEventListener('click', handleClick, true);
      document.body.style.userSelect = 'auto';
      document.body.style.webkitUserSelect = 'auto';
      document.body.style.mozUserSelect = 'auto';
      document.body.style.msUserSelect = 'auto';
      const style = document.getElementById('kids-mode-style');
      if (style) {
        style.remove();
      }
    };
  }, [isParentMode]);

  const handleParentModeToggle = () => {
    if (isParentMode) {
      setIsParentMode(false);
      setActiveTab('videos');
    } else {
      setShowPinEntry(true);
    }
  };

  const handlePinSuccess = () => {
    setIsParentMode(true);
    setShowPinEntry(false);
    setActiveTab('channels');
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <header className="bg-white shadow-sm">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <h1 className="text-2xl font-bold text-blue-600 flex items-center gap-2">
              <Shield className="w-8 h-8" />
              YouTube Kids Safe
            </h1>
            <button
              onClick={handleParentModeToggle}
              className={`px-4 py-2 rounded-lg flex items-center gap-2 ${
                isParentMode
                  ? 'bg-red-500 text-white hover:bg-red-600'
                  : 'bg-blue-500 text-white hover:bg-blue-600'
              }`}
              aria-label={isParentMode ? 'Exit Parent Mode' : 'Enter Parent Mode'}
            >
              <Lock className="w-4 h-4" />
              {isParentMode ? 'Exit Parent Mode' : 'Parent Mode'}
            </button>
          </div>
        </div>
      </header>

      {isParentMode && (
        <nav className="bg-gray-200 border-b">
          <div className="container mx-auto px-4">
            <div className="flex space-x-8">
              <button
                onClick={() => setActiveTab('channels')}
                className={`py-3 px-4 border-b-2 font-medium ${
                  activeTab === 'channels'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-600 hover:text-gray-800'
                }`}
                aria-label="View Channels"
              >
                Channels
              </button>
              <button
                onClick={() => setActiveTab('settings')}
                className={`py-3 px-4 border-b-2 font-medium ${
                  activeTab === 'settings'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-600 hover:text-gray-800'
                }`}
                aria-label="View Settings"
              >
                Settings
              </button>
              <button
                onClick={() => setActiveTab('videos')}
                className={`py-3 px-4 border-b-2 font-medium ${
                  activeTab === 'videos'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-600 hover:text-gray-800'
                }`}
                aria-label="Preview Kids View"
              >
                Preview Kids View
              </button>
            </div>
          </div>
        </nav>
      )}

      <main className="container mx-auto px-4 py-8">
        {!isParentMode && (
          <div className="mb-6">
            <h2 className="text-xl font-semibold mb-4">Safe Videos for Kids</h2>
            <VideoGrid />
          </div>
        )}

        {isParentMode && activeTab === 'channels' && <ChannelManager />}
        {isParentMode && activeTab === 'settings' && <Settings />}
        {isParentMode && activeTab === 'videos' && (
          <div>
            <div className="bg-yellow-100 border border-yellow-400 text-yellow-800 px-4 py-3 rounded-lg mb-6">
              <p className="font-semibold">Preview Mode</p>
              <p className="text-sm">This is how kids will see the app. Only videos from approved channels will appear.</p>
            </div>
            <VideoGrid />
          </div>
        )}
      </main>

      {showPinEntry && (
        <PinEntry
          onSuccess={handlePinSuccess}
          onCancel={() => setShowPinEntry(false)}
        />
      )}

      <InstallPrompt />
    </div>
  );
};

export default App;